.device {
	cursor: pointer;
	padding: 1rem;
	/* min-height: 100px; */

	color: #ffffff;
	background: #0a0807;
	box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
	border-radius: 6px;

	-webkit-transition: background-color 0.2s linear;
	-ms-transition: background-color 0.2s linear;
	transition: background-color 0.2s linear;

	display: flex;
	align-items: flex-start;
	gap: 0.5rem;
}

.device.active {
	color: #0a0807;
	background: #ffffff;
}

.device__info {
	display: flex;
	flex-direction: column;
	gap: 0.25rem;
}

.device__name {
	font-weight: 600;
}

.device__status {
	font-size: small;
}
