.app {
	position: relative;

	min-height: 100vh;
	background-color: red;
}

.banner {
	padding: 1rem;
	min-height: 300px;

	background: rgb(34, 193, 195);
	background: linear-gradient(
		90deg,
		rgba(34, 193, 195, 1) 0%,
		rgba(253, 187, 45, 1) 100%
	);
}

.banner__logo {
	margin-bottom: 1rem;

	display: flex;
	flex-direction: column;
}

.logo__icon {
	font-size: 40px;
}

.logo__name {
	font-size: 30px;
	font-weight: 900;

	line-height: 0.8;
}

.banner__author {
	font-weight: 700;
}

.devices {
	position: relative;
	top: -200px;

	max-width: 900px;
	margin: 0 auto;

	display: grid;
	grid-template-columns: repeat(3, 1fr);
	gap: 0.5rem;
}

.devices__title {
	text-align: center;
	padding: 0.5rem;
	margin-bottom: 1rem;
	width: 100%;

	grid-column: 1/4;
}

.card-dark:hover {
	background-color: #ad310b;
}
